import { __decorate } from "tslib";
import { Component, Vue, Prop } from 'vue-property-decorator';
import { deepClone } from '@/until';
import imageUpload from '@/components/common-components/image-upload.vue';
import textButton from '@/components/common-components/text-btn.vue';
let AddForm = class AddForm extends Vue {
    constructor() {
        super(...arguments);
        // ----------------内部属性-----------------------
        this.validity = {};
        this.valid = undefined;
        this.model = {
            name: '',
            price: 0,
            num: 0,
            img: '',
            total: 0,
            goodsType: 'delivery' // 商品类型 1.delivery 配送型 2.arrival 到店核销
        };
        this.imageList = [];
        this.isEdit = false;
        this.fields = [
            {
                type: 'input',
                modelKey: 'name',
                label: '商品名称',
                props: {
                    placeholder: '请输入'
                },
                rules: {
                    required: true
                },
                trigger: 'blur',
                messages: { required: '商品名称必填' },
            },
            {
                type: 'input',
                modelKey: 'price',
                label: '价格',
                props: {
                    placeholder: '请输入'
                },
                rules: {
                    required: true,
                    custom: (val) => {
                        return val > 0;
                    }
                },
                messages: { required: '价格必填', custom: '价格需要大于等于0元' },
                trigger: 'blur'
            }
        ];
        this.goodsTypeOptions = [
            {
                label: '帮手配送',
                value: 'delivery'
            },
            {
                label: '到店核销',
                value: 'arrival'
            }
        ];
        this.options = {
            scrollToInvalidField: true,
            layout: 'classic' // classic fresh
        };
        // ----------------生命周期-----------------------
    }
    // ----------------计算属性-----------------------
    get schoolInfo() {
        return this.$store.state.schoolInfo;
    }
    // ----------------监听方法-----------------------
    // ----------------调用方法-----------------------
    resetHandler() {
        this.$emit('cancel');
    }
    submitHandler(e) {
        e.preventDefault();
        let menu = deepClone(this.model.menu) || [];
        let modelId = this.model.id;
        // 整理规格id
        let setMenu = menu.filter((item) => {
            return item.spec && item.price > 0;
        }).map((item, index) => {
            item.id = modelId + '-' + index;
            item.price = Number(item.price);
            return item;
        });
        if (!setMenu || setMenu.length <= 0) {
            // 兼容老数据
            setMenu = null;
        }
        this.model.menu = setMenu;
        // 数字化价格
        this.model.price = Number(this.model.price);
        if (this.imageList.length > 0) {
            this.model.img = this.imageList[0];
        }
        else {
            this.model.img = 'http://mmbiz.qpic.cn/mmbiz_png/5XHic5LQPCDjZwNLhL8Opj5Mkpib5mZgFLSmZDOO7dQP3yezUpIMIucia9yLL0M2Cft1Z1nicCErZByDN7teCYefiaA/0';
        }
        console.log(this.model, 'this.modellllllllllllll');
        this.$emit('change', this.model);
    }
    setData(model) {
        if (model.img) {
            this.$set(this, 'imageList', [model.img]);
        }
        const initMode = {
            ...deepClone(this.model),
            ...model
        };
        this.$set(this, 'model', initMode);
    }
    addSpec() {
        let menu = (this.model.menu && this.model.menu.length > 0) ? deepClone(this.model.menu) : [];
        let index = (menu && menu.length) ? menu.length : 0;
        let specObj = {
            id: this.model.id + '' + index,
            num: 0,
            price: 0,
            spec: ''
        };
        menu.push(specObj);
        this.$set(this.model, 'menu', menu);
    }
    deleteSpec(index) {
        console.log(index, '');
        let menu = deepClone(this.model.menu);
        menu.splice(index, 1);
        this.$set(this.model, 'menu', menu);
    }
    submitClick() {
        this.$refs.editForm.submit(false);
    }
    resetClick() {
        this.$refs.editForm.reset();
    }
};
