__decorate([
    Prop({ default: '' })
], AddForm.prototype, "shopName", void 0);
AddForm = __decorate([
    Component({
        name: 'addForm',
        components: {
            imageUpload,
            textButton
        }
    })
], AddForm);
export default AddForm;
