import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import rightBox from '@/components/common-components/right-box.vue';
import editBox from '@/components/common-components/edit-box.vue';
import editForm from './edit-food.vue';
import addNav from './add-nav.vue';
import { deepClone } from '@/until';
import textButton from '@/components/common-components/text-btn.vue';
let businessManagement = class businessManagement extends Vue {
    constructor() {
        super(...arguments);
        this.showEditbox = false;
        this.showAddNav = false;
        this.pageTitle = '商品管理';
        this.defaultImg = 'http://mmbiz.qpic.cn/mmbiz_png/5XHic5LQPCDjZwNLhL8Opj5Mkpib5mZgFLSmZDOO7dQP3yezUpIMIucia9yLL0M2Cft1Z1nicCErZByDN7teCYefiaA/0';
        this.data = [];
        this.currentIndex = 0;
        this.modelIndex = 0;
    }
    get shopId() {
        return this.$route.params.id;
    }
    get shopName() {
        return this.$route.query.shopName;
    }
    created() {
        let shopData = deepClone(this.$route.params.shopData);
        let setShopData = shopData.map((item) => {
            let currentItem = item;
            if (currentItem.menu && currentItem.menu.length > 0) {
                let currentMenu = currentItem.menu.map((menuitem) => {
                    if (!menuitem.sellout) {
                        menuitem.sellout = false;
                    }
                    return menuitem;
                });
                currentItem.menu = currentMenu;
            }
            return currentItem;
        });
        this.$set(this, 'data', setShopData);
    }
    navClick(index) {
        this.currentIndex = index;
        this.$refs.rightBox.scrollTop = 0;
    }
    editMenu(menu, index) {
        this.modelIndex = index;
        let cloneMenu = deepClone(menu);
        this.showEditbox = true;
        this.$nextTick(() => {
            this.$refs.editForm.setData(cloneMenu);
        });
    }
    deleteMenu(menu, index) {
        this.$createDialog({
            type: 'confirm',
            content: '确定删除【' + menu.name + '】吗？',
            onConfirm: (e) => {
                let currentMenu = deepClone(this.data[this.currentIndex].menu);
                currentMenu.splice(index, 1);
                this.$set(this.data[this.currentIndex], 'menu', currentMenu);
            }
        }).show();
    }
    changeSuccess(model) {
        this.$set(this.data[this.currentIndex].menu, this.modelIndex, model);
        this.showEditbox = false;
    }
    navChangeSuccess(model) {
        let currentModel = deepClone(model);
        if (!currentModel.id) {
            let addObj = {
                id: 'C' + (this.data.length ? this.data.length : 0),
                name: currentModel.name,
                menu: []
            };
            this.data.push(addObj);
        }
        this.showAddNav = false;
    }
    addNav() {
        this.showAddNav = true;
    }
    addFood() {
        let menu = this.data[this.currentIndex].menu || [];
        let id = this.data[this.currentIndex].id + '-' + menu.length;
        this.editMenu({ id: id, name: '', price: 0, num: 0 }, menu.length);
    }
    saveFood() {
        // 重设商品id 
        let setData = this.data.map((item, index) => {
            item.id = 'C' + index;
            if (item.menu && item.menu.length > 0) {
                item.menu = item.menu.map((menuItem, menuIndex) => {
                    menuItem.id = item.id + '-' + menuIndex;
                    if (menuItem.menu) {
                        menuItem.menu = menuItem.menu.map((menuChildItem, menuChildIndex) => {
                            menuChildItem.id = menuItem.id + '-' + menuChildIndex;
                            return menuChildItem;
                        });
                    }
                    return menuItem;
                });
            }
            return item;
        });
        this.$store.dispatch('updateShopInfo', {
            shopId: this.shopId,
            type: 'shopData',
            value: setData
        }).then((e) => {
            if (e.success) {
                this.$createDialog({
                    type: 'alert',
                    content: '保存成功',
                    onConfirm: (e) => {
                        this.$router.push({
                            name: 'businessManagement',
                            params: {
                                id: this.shopId,
                                oprate: 'update',
                                type: 'shopData',
                                data: this.data
                            }
                        });
                    }
                }).show();
            }
        });
    }
};
businessManagement = __decorate([
    Component({
        components: {
            page,
            rightBox,
            editBox,
            editForm,
            addNav,
            textButton
        }
    })
], businessManagement);
export default businessManagement;
